/*
 *  Hero Image/Content Banner
 *    Styles for the hero content/image banner section.
 *
 *  Version:
 *    1.0.0 - 2021/11/18
 */

.c-hero-image-content {
  background-color: $s-color--background-focus;

  &__inner {
    padding-bottom: 4.9375rem;
    margin: 0 auto;
  }

  &__image-container {
    padding-bottom: 86.4%;
  }

  &__content {
    padding: 0 $s-gutter--edge-mobile;
    max-width: 41.875rem;
    margin: 0 auto;
  }

  &__title {
    margin-top: 3.6875rem;
  }

  &__text {
    margin-left: 2.9375rem;
  }

  @include t-media-query($s-small-up) {

    &__content {
      padding: 0 2.75rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      padding-bottom: 0;
    }

    &__image-container {
      width: 50%;
      padding-bottom: 45.15%;
    }

    &__content {
      width: 50%;
      margin: 0;
      padding: 1.25rem 4.75rem 3.125rem 2.75rem;
    }

    &__text {
      margin-left: 3rem;
      padding-bottom: 2rem;
    }

    &__title{
      margin-top: 2rem;
    }
  }
  @include t-media-query($s-large-up) {
    &__inner{
      padding-bottom: 0;
    }

    &__content {
      padding: 0;
      width: 33.5%;
      margin:0 10.5% 0 6%;
    }

  }

  @include t-media-query($s-xlarge-up) {
    &__title {
      margin: 1.5rem 0 1.5625rem;
    }

    &__text {
      margin-left: 7rem;
      padding: 0;
    }
  }
}